<template>
<main class="content container">
    <div class="content__top">
      <div class="content__row">
        <h1 class="content__title">
          Каталог
        </h1>
        <span class="content__info">
          {{products.length}} товара
        </span>
      </div>
    </div>
    
        <div class="content__items-per-page">
          <label for="itemsPerPage">Товаров на странице:</label>
          <select id="itemsPerPage" v-model="productsPerPage" @change="productPerPageChange">
            <option v-for="option in productPerPage" :key="option" :value="option">{{ option }}</option>
          </select>
        </div>
      
    <div class="content__catalog">
      <aside class="filter">
        <ProductFilter
         v-model:price-from="filterPriceFrom"
         v-model:price-to="filterPriceTo"
         v-model:category-id="filterCategoryId"
         v-model:colorId="filterColorId"
         v-model:materialsId="filterMaterialId"
         v-model:sesonsId="filterSeasonId"
         />
        </aside>
      
      <section class="catalog">
        
        <div v-if="productsLoading"><PreloadSpin /></div>
        <div v-if="productsLoadingFailed">Ошибка при загруске товаров .. <button @click.prevent="loadProducts">Попробовать еще раз</button></div>
<ProductList :products="products"/>

<BasePagination v-model="page" :count="countProducts" v-model:per-page="productsPerPage"/>
</section>
      
    </div>
  </main>
</template>

<script>
import PreloadSpin from '@/components/PreloadSpin';
import ProductList from '@/components/ProductList';
import BasePagination from '@/components/BasePagination';
import ProductFilter from '@/components/ProductFilter';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
components: {ProductList, BasePagination, ProductFilter, PreloadSpin},
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: 0,
      filterMaterialId: [],
      filterSeasonId: [],

      page: 1,
      productsPerPage: 12,
      
      productsData: null,
      productPerPage: [12,24,32],
      productsLoading: false,
      productsLoadingFailed: false,
    }
  },
  computed: {
    products(){
      return this.productsData
       ? this.productsData.items.map(product => {
        try {
          const color = product.colors && product.colors[0];
          const gallery = color && color.gallery && color.gallery[0];
          const imageUrl = gallery && gallery.file && gallery.file.url;
          if(imageUrl) {
            return {
              ...product,
              image: imageUrl,
            };
          }else{
            return {
              ...product,
              image: 'img/no-image.jpg',
            };
          }
            
          // eslint-disable-next-line no-empty
          } catch (error) {

          }
       })
       
       : [];
    },
    countProducts(){
      return this.productsData ? this.productsData.pagination.total : 0;
    }
  },
  methods: {
    loadProducts(){
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
      .get(API_BASE_URL + '/api/products', {
          params: {
            page: this.page,
            limit: this.productsPerPage,            
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
            categoryId: this.filterCategoryId,
            colorIds: this.filterColorId,
            materialIds: this.filterMaterialId,
            seasonIds: this.filterSeasonId
          }
      })
      .then(response => this.productsData = response.data)
      .catch(() => this.productsLoadingFailed = true)
      .then(() => this.productsLoading = false);
      }, 300);
    },
    productPerPageChange() {
    this.loadProducts();
  },
  },
  watch: {
      page(){
        this.loadProducts();
      },
      filterPriceFrom(){
        this.loadProducts();
      },
      filterPriceTo(){
        this.loadProducts();
      },
      filterCategoryId(){
        this.loadProducts();
      },
      filterColorId(){
        this.loadProducts();
      },
      filterMaterialId(){
        this.loadProducts();
      },
      filterSeasonId(){
        this.loadProducts();
      }
    },
  created(){
    this.loadProducts();
  }
}
</script>

<style>
.content__items-per-page {
  float: right;
  position: relative;
  top: -5em;
}
</style>