import {createStore} from 'vuex'
import axios from "axios";
import { API_BASE_URL } from "@/config";

export default createStore({
    state: {
        cartProducts: [],
        userAccessKey: null,
        cartLoad: false,
        cartLoadFailed: false,
        cartProductsData: [],
        orderInfo: null
    },
    mutations: {
        updateOrderInfo(state, orderInfo){
            state.orderInfo = orderInfo;
        },
        resetCart(state){
            state.cartProducts = [];
            state.cartProductsData = [];
        },
        updateCartProductAmount(state, {basketItemId, amount}){
            const item = state.cartProducts.find(item => item.basketItemId === basketItemId);
            if(item){
                item.amount = amount;
            }
        },
        removeCartProduct(state, basketItemId) {
            state.cartProducts = state.cartProducts.filter(item => item.basketItemId !== basketItemId);
            axios
              .delete(API_BASE_URL + '/api/baskets/products', {
                data: {
                    basketItemId: basketItemId,
                },
                params: {
                  userAccessKey: state.userAccessKey,
                },
              })
              .then(response => {
                state.cartProductsData = response.data.items;
              })
              .catch(error => {
                console.log(error);
              });
          },
        updateUserAccessKey(state, accessKey){
            state.userAccessKey = accessKey;
        },
        updateCartProductsData(state, items){
            state.cartProductsData = items;
        },
        syncCartProducts(state){
            state.cartProducts = state.cartProductsData.map(item => {
                return {
                    productId: item.product.id,
                    amount: item.quantity,
                    colorId: item.color.color.id,
                    sizeId: item.size.id,
                    basketItemId: item.id
                }
            });
        },
        setLoadCart(state, isLoading) {
            state.cartLoad = isLoading;
          },
        setFailedLoadCart(state, hasFailed) {
            state.cartLoadFailed = hasFailed;
          }
    },
    getters: {
        cartDetailProducts(state){
            return state.cartProducts.map(item => {
                const product = state.cartProductsData.find(p => p.product.id === item.productId).product;

                return {
                    ...item,
                    product
                }
            });
        },
        
        
        cartTotalPrice(state, getters){
            return getters.cartDetailProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
        }
    },
    actions: {
        loadOrderInfo(context, orderId){
            return axios
            .get(API_BASE_URL + '/api/orders/' + orderId, {
               params:{
                userAccessKey: context.state.userAccessKey
               }
            })
            .then(response => {
                context.commit('updateOrderInfo', response.data);
            });
        },
        loadCart(context){
            context.commit('setLoadCart', true);
            context.commit('setFailedLoadCart', false);
            clearTimeout(this.loadCartProductTimer);
            this.loadCartProductTimer = setTimeout(() => {
         return axios
            .get(API_BASE_URL + '/api/baskets', {
               params:{
                userAccessKey: context.state.userAccessKey
               }
            })
            .then(response => {
                if(!context.state.userAccessKey){
                    localStorage.setItem('userAccessKey', response.data.user.accessKey);
                    context.commit('updateUserAccessKey', response.data.user.accessKey);
                }

                context.commit('updateCartProductsData', response.data.items);
                context.commit('syncCartProducts');
                })
                .catch(() => context.commit('setFailedLoadCart', true))
                .then(() => context.commit('setLoadCart', false));
            }, 500);
        },
        addProductToCart(context, {productId, amount, colorId, sizeId}){
           return (new Promise(resolve => setTimeout(resolve, 2000)))
            .then(() => {
                return axios
                        .post(API_BASE_URL + '/api/baskets/products', {
                        productId: productId,
                        colorId: colorId,
                        sizeId: sizeId,
                        quantity: amount
                        }, {
                            params: {
                                userAccessKey: context.state.userAccessKey
                            }
                        })
                        .then(response => {
                            context.commit('updateCartProductsData', response.data.items);
                            context.commit('syncCartProducts');
                            return response;
                        })
            })

        },
        updateCartProductAmount(context, {basketItemId, amount}){
           context.commit('updateCartProductAmount', {basketItemId, amount});

            if(amount < 1){
                return;
            }
            
            return axios
                    .put(API_BASE_URL + '/api/baskets/products', {
                    basketItemId: basketItemId,
                    quantity: amount,
                    }, {
                        params: {
                            userAccessKey: context.state.userAccessKey
                        }
                    })
                    .then(response => {
                        context.commit('updateCartProductsData', response.data.items);
                        
                    })
                    .catch(() => {
                        context.commit('syncCartProducts');
                    })
        },
        deleteCart(context, basketItemId) {
            context.commit('removeCartProduct', basketItemId);
          },
    }
});