<template>
<li v-bind="$attrs" v-for="product in productsNormalized" :key="product.id">
            <router-link class="catalog__pic" :to="{name: 'product', params: {id: product.id}}">
              
              <img :src="imageChange(product)" :alt="product.title">
           
            </router-link>

            <router-link :to="{name: 'product', params: {id: product.id}}">
              <h3 class="catalog__title">
              <a href="#">
                {{product.title}}
              </a>
            </h3>
            </router-link>

            <span class="catalog__price">
              {{ product.pricePretty }} ₽
            </span>
            <div class="catalog__wrapper">
            <ul class="colors colors--black">
              <li class="colors__item" v-for="color in product.colors" :key="color.id">
                <label class="colors__label" @click.prevent="changeImage(color)">
                  <input class="colors__radio sr-only" type="radio" :value="color" v-model.number="selectedColor">
                  <span class="colors__value" :style="{backgroundColor: color.color.code}">
                  </span>
                </label>
              </li>
            </ul>
                <button class="button btn-cart btn-cart--add">
                  <span class="btn-cart__icon">
                    <fa class="btn-icons" icon="fa-solid fa-cart-plus" @click.prevent="openQuickView(product.id)"/> 
                  </span>
                </button>
              
              
            
          </div>
          
            
          </li>
          <BaseModal v-model:open="isQuickViewOpen">
            <ProductQuickView :product-id="currentProductId"/>
          </BaseModal>
</template>

<script>
import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';
import BaseModal from './BaseModal.vue';
import { defineAsyncComponent, h } from 'vue';

export default {
  inheritAttrs: false,
  components: { 
    BaseModal, 
    ProductQuickView: defineAsyncComponent({
      loader: () => import('@/components/ProductQuickView.vue'),
      delay: 0,
      loadingComponent: () => h('div', 'Загрузка...')
    })
   },
data(){
  return {
    selectedColor: null,
    currentProductId: null
  };
},
computed:{
  isQuickViewOpen: {
    get() {
      return !!this.currentProductId;
    },
    set(isOpen){
      if (!isOpen){
        this.currentProductId = null;
      }
    }
  },
  productsNormalized() {
        return this.products.map((product) => {
          return {
            ...product,
            pricePretty: numberFormat(product.price),
          }
        });
      }
 },
methods: {
  gotoPage,
  openQuickView(productId){
    this.currentProductId = productId;
  },
  
  changeImage(color){
    this.selectedColor = color;
    //console.log('Clicked on color:', color);
    // console.log('Selected Color Object:', this.selectedColor);
    // if (this.selectedColor && this.selectedColor.gallery && this.selectedColor.gallery.length > 0) {
    //   console.log('Image URL:', this.selectedColor.gallery[0].file.url);
    // }
    // if (this.selectedColor && this.selectedColor.id) {
    //   console.log('Selected Color ID:', this.selectedColor.id);
    // }
  },
  imageChange(product) {
    if (product && this.selectedColor !== null) {
      const selectedColorObject = product.colors.find(color => color.id === this.selectedColor.id);
      if (selectedColorObject && selectedColorObject.gallery && selectedColorObject.gallery.length > 0) {
        return selectedColorObject.gallery[0].file.url;
      }
    }
    return product.image || '';
  }
},
 props: ['products']
 
};
</script>

<style>
.catalog__wrapper{
  display: flex;
    justify-content: space-between;
    align-items: center;
}
.btn-cart, .btn-cart__icon {
    display: flex;
    align-items: center;
    justify-content: center;
}
.btn-cart{
    padding: 10px 10px 8px;
    background-color: #e02d71;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    font-size: 14px;
    color: #fff;
}
.btn-cart, .btn-cart__icon{
    display: flex;
    align-items: center;
    justify-content: center;

}
.btn-icons{
  font-size: 25px;
}

</style>