<template>
  <main class="content container" v-if="productStatus.isLoading"><PreloadSpin /></main>
  <main class="content container" v-else-if="productStatus.isFailed"><div>Не удалось загрузить товар</div></main>
    <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            {{category.title}}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{product.title}}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img width="570" height="570" :src="productImage || 'img/no-image.jpg'" :alt="product.title">
        </div>
        <ul class="pics__list">
          <li class="pics__item" v-for="(color, index) in product ? product.colors : []" :key="index">
            <a href="#" @click.prevent="changeImage(color)" :class="{'pics__link': true, 'pics__link--current': selectedColor === color.id }">
              <img width="98" height="98" :src="color.gallery ? color.gallery[0].file.url : 'img/no-image.jpg'" :alt="product.title">
            </a>
          </li>
        </ul>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{product.id}}</span>
        <h2 class="item__title">
          {{product.title}}
        </h2>
        <div class="item__form">
          <form class="form" action="#" method="POST" @submit.prevent="doAddToCart">
            <div class="item__row item__row--center">
              <ProoductQnty :productAmount="productAmount" @productAmount-changed="updateQuantity"/>              
              <b class="item__price">
                {{ product.pricePretty }} ₽
              </b>
            </div>

            <div class="item__row">
              <fieldset class="form__block">
                <legend class="form__legend">Цвет</legend>
                <ul class="colors colors--black">
                  <li class="colors__item" v-for="(color, index) in product.colors" :key="index">
                    <label class="colors__label">
                      <input class="colors__radio sr-only" type="radio" name="color-item" :value="color.id" v-model.number="selectedColor">
                      <span class="colors__value" :style="{ backgroundColor: color.color.code }">
                      </span>
                    </label>
                  </li>
                </ul>
              </fieldset>


              <fieldset class="form__block">
                <legend class="form__legend">Размер</legend>
                <label class="form__label form__label--small form__label--select" >
                  <select class="form__select" name="category" v-model="currentSize">
                    <option :value="sizes.id" v-for="sizes in size" :key="sizes.id" >{{sizes.title}}</option>
                  </select>
                </label>
              </fieldset>
            </div>
            <button class="item__button button button--primery" type="submit" :disabled="productStatus.isSendingProduct">
              {{ productStatus.isSendingProduct ? 'Добавляем товар в корзину...' : 'В корзину' }}
            </button>

            <div v-show="productStatus.isAddedProduct">Товар добавлен в корзину</div>

            <BaseModal v-model:open="isShowAddedMessage">
            Товар добавлен в корзину</BaseModal>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link" :class="{ 'tabs__link--current': activeTab === 'info' }" @click="setActiveTab('info')">
              Информация о товаре
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" :class="{ 'tabs__link--current': activeTab === 'delivery' }" @click="setActiveTab('delivery')">
              Доставка и возврат
            </a>
          </li>
        </ul>

        <div class="item__content">
          <div v-if="activeTab === 'info'">
          <h3>Сезоны:</h3>
            <ul>
              <li v-for="sezon in product.seasons" :key="sezon.id">
                <p>{{ sezon.title }}</p>
              </li>
            </ul>    
          <h3>Материалы:</h3>
            <ul>
              <li v-for="material in product.materials" :key="material.id">
                <p>{{ material.title }}</p>
              </li>
            </ul>
          </div>
            
          <div class="item__content" v-else-if="activeTab === 'delivery'">
            <h3>Доставка:</h3>
                  <p>
                    1. Курьерская доставка по Москве и Московской области – 290 ₽<br>
                    2.Самовывоз из магазина. Список и адреса магазинов Вы можете посмотреть здесь<br>
                  </p>
                
                  <h3>Возврат:</h3>

                  <p>
                    Любой возврат должен быть осуществлен в течение Возвраты в магазине БЕСПЛАТНО.<br>
                    Вы можете вернуть товары в любой магазин. Магазин должен быть расположен в стране, в которой Вы осуществили покупку.
                    БЕСПЛАТНЫЙ возврат в Пункт выдачи заказов.<br>
                    Для того чтобы вернуть товар в одном из наших Пунктов выдачи заказов, позвоните по телефону 8 800 600 90 09<br>
                  </p>
                </div>
         </div>
       </div>
    </section>
  </main>

</template>

<script>
import ProoductQnty from '@/components/ProoductQnty.vue';
import PreloadSpin from '@/components/PreloadSpin.vue';
import { useStore } from 'vuex';
import BaseModal from '@/components/BaseModal.vue';
import {defineComponent, ref } from 'vue';
import { useRoute } from 'vue-router';
import useProduct from '@/hooks/useProduct';

export default defineComponent({

  components: {ProoductQnty, PreloadSpin, BaseModal},

  setup() {
  const store = useStore();
  const $route = useRoute();
  const {
    product,
    category,
    productAmount,
    currentSize,
    selectedColor,
    isShowAddedMessage,
    size,
    image: productImage,
    changeImage,
    isCurrentColor,
    updateQuantity,

    doAddToCart,
    fetchProduct,
    status: productStatus
    } = useProduct(store);
  
  const activeTab = ref('info');  

  const setActiveTab = (tab) => {
    activeTab.value = tab;
  };

  fetchProduct($route.params.id);

  return {
    activeTab,
    productAmount,
    productData: product,
    productStatus,
    currentSize,
    selectedColor,
    isShowAddedMessage,
    product,
    category,
    size,
    productImage,
    doAddToCart,
    setActiveTab,
    changeImage,
    isCurrentColor,
    updateQuantity,
  };
}

});

</script>