<template>
<main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span v-if="orderInfoData">№ {{orderInfoData.id}}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа. 
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                Получатель
              </span>
              <span class="dictionary__value" v-if="orderInfoData">
                {{ orderInfoData.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Адрес доставки
              </span>
              <span class="dictionary__value" v-if="orderInfoData">
                {{ orderInfoData.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Телефон
              </span>
              <span class="dictionary__value" v-if="orderInfoData">
                {{ orderInfoData.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value" v-if="orderInfoData">
                {{ orderInfoData.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Способ оплаты
              </span>
              <span class="dictionary__value" v-if="orderInfoData">
                <b>{{ orderInfoData.paymentType }}</b>
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block" v-if="orderInfoData && orderInfoData.basket && orderInfoData.basket.items">
          <ul class="cart__orders" v-for="item in orderInfoData.basket.items" :key="item.id">
            <li class="cart__order">
              <h3>{{item.product.title}}</h3>              
              <b>{{item.product.price}} ₽</b>  
              <span>SKU: {{ generateSku(item.product.id, item.color.color.id, item.size.id) }}</span>
              <span>Цвет: {{ item.color.color.title}}, Кол-во: {{item.quantity }} шт, Размер: {{ item.size.title }}</span>
            </li>            
          </ul>
          
          <div class="cart__total">
            <p>Доставка: 
              <b>{{ orderInfoData.deliveryType.title }}</b><br>
              <b>{{ orderInfoData.deliveryType.price }} ₽</b></p>
            <p>Итого: <b>{{orderInfoData.basket.items.length }}</b> товара на сумму <b>{{orderInfoData.totalPrice}} ₽</b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { mapState } from 'vuex';
import  useProduct from '@/hooks/useProduct';

export default{
  setup() {
    const { generateSku } = useProduct();
    return {
      generateSku
    };
  },
computed: {
  ...mapState({
    data: state => state.orderInfo
  }),
  orderInfoData(){
    return this.$store.state.orderInfo;
  }
},
created(){
if(this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
  return;
}

  this.$store.dispatch('loadOrderInfo', this.$route.params.id);
}
}
</script>