<template>
<main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">
          Оформление заказа
        </h1>
      </div>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText v-model="formData.name" :error="formError.name" title="ФИО" placeholder="Введите ваше полное имя"/>
            <BaseFormText v-model="formData.address" :error="formError.address" title="Адрес доставки" placeholder="Введите ваш адрес"/>
            <BaseFormText v-model="formData.phone" :error="formError.phone" title="Телефон" placeholder="Введите ваш телефон"/>
            <BaseFormText v-model="formData.email" :error="formError.email" title="Email" placeholder="Введи ваш Email"/>
            <BaseFormTextArea v-model="formData.comment" :error="formError.comment" title="Комментарий к заказу" placeholder="Ваши пожелания"/>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item" v-for="deliveryOption in deliveryOptions" :key="deliveryOption.id">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    :name="'delivery_' + deliveryOption.id"
                    :value="deliveryOption.id"
                    v-model="selectedDeliveryOption"
                  />
                  <span class="options__value">
                    {{ deliveryOption.title }} <b>{{ deliveryOption.price > 0 ? deliveryOption.price + ' ₽' : 'бесплатно' }}</b>
                  </span>
                </label>
              </li>
            </ul>
          
            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item" v-for="paymentOption in paymentOptions" :key="paymentOption.id">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    :name="'payment_' + paymentOption.id"
                    :value="paymentOption.id"
                    v-model="selectedPaymentOption"
                  />
                  <span class="options__value">{{ paymentOption.title }}</span>
                </label>
              </li>
            </ul>

          </div>
          <div v-if="formError.delivery" class="cart__error">{{ formError.delivery }}</div>
          <div v-if="formError.payment" class="cart__error">{{ formError.payment }}</div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders" v-for="(item, index) in cartDetailProducts" :key="index">
            <li class="cart__order">
              <h3>{{ item.product.title }}</h3>
              <b>{{ item.product.price }} ₽</b>
              <span> SKU: {{ generateSku(item.product.id, item.colorId, item.sizeId) }}
              <p></p>
                <div class="row">
                  <div class="cell">Цвет: <div class="cell colors__value"
                    v-if="item.colorId"
                    :style="{ background: item.product.colors.find(c => c.color.id === item.colorId).color.code }">
                  </div>
                </div>
                  
                Размер:
                <span class= "size__item" v-if="item.sizeId"> {{ this.$store.state.cartProductsData.find(s => s.size.id === item.sizeId).size.title }}</span>
                </div>
            </span> 
                
            </li>
          </ul>
          
          <div class="cart__total">
            <p>Доставка: <b>
              {{ selectedDeliveryOption !== null
                ? getDeliveryPrice(selectedDeliveryOption) 
                : 0
              }} ₽</b></p>
            <p>Оплата: <b>{{ selectedPaymentOption ? getPaymentTitle(selectedPaymentOption) : 'Не выбрано' }}</b></p>
            <p>Итого: <b>{{ $store.state.cartProducts.length }}</b> товара на сумму <b>{{ cartTotalOrder }} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>          
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextArea from '@/components/BaseFormTextArea.vue';
import useProduct from '@/hooks/useProduct';
import { mapGetters } from 'vuex';
import { API_BASE_URL } from '@/config';
import axios from 'axios';

export default {
  setup() {
    const { generateSku } = useProduct();
    return {
      generateSku
    };
  },
    components: {BaseFormText, BaseFormTextArea},
    data(){
        return {
            formData: {},
            formError: {},
            formErrorMessage: '',
            deliveryOptions: [],
            paymentOptions: [],
            selectedDeliveryOption: null,
            selectedPaymentOption: null,
        }
    },
    computed: {
      ...mapGetters(['cartDetailProducts', 'cartTotalPrice']),      
      cartTotalOrder() {
        const deliveryPrice = this.selectedDeliveryOption !== null ? this.getDeliveryPrice(this.selectedDeliveryOption) : 0;
        return parseFloat(this.cartTotalPrice) + parseFloat(deliveryPrice);
      },
    },
    methods: {
        order(){
            this.formError = {};
            this.formErrorMessage = '';

            if (!this.selectedDeliveryOption) {
              this.formError.delivery = 'Выберите способ доставки';
            }

            if (!this.selectedPaymentOption) {
              this.formError.payment = 'Выберите способ оплаты';
            }
            
            if (Object.keys(this.formError).length > 0) {
              return;
            }
            axios
            .post(API_BASE_URL + '/api/orders', {
              ...this.formData,
               deliveryTypeId: this.selectedDeliveryOption,
               paymentTypeId: this.selectedPaymentOption,
            }, {
                  params:{
                    userAccessKey: this.$store.state.userAccessKey,                   
                  }
            })
            .then(response => {
                this.$store.commit('resetCart');
                this.$store.commit('updateOrderInfo', response.data);
                this.$router.push({name: 'orderInfo', params: {id: response.data.id}});
            })
            .catch(error => {
                this.formError = error.response.data.error.request || {};
                this.formErrorMessage = error.response.data.error.message;
            })
        },
          fetchDeliveryOptions() {
            axios.get(API_BASE_URL + '/api/deliveries').then((response) => {
              this.deliveryOptions = response.data;
              this.selectedDeliveryOption = this.deliveryOptions.length > 0 ? this.deliveryOptions[0].id : null;
              this.fetchPaymentOptions();
            });
          },
          fetchPaymentOptions() {
            if (this.selectedDeliveryOption !== null) {
              axios.get(API_BASE_URL + `/api/payments?deliveryTypeId=${this.selectedDeliveryOption}`).then((response) => {
                this.paymentOptions = response.data;
                this.selectedPaymentOption = this.paymentOptions.length > 0 ? this.paymentOptions[0].id : null;
              });
            }
          },
          getDeliveryPrice(deliveryOptionId) {
            const selectedDelivery = this.deliveryOptions.find(option => option.id === deliveryOptionId);
            return selectedDelivery ? selectedDelivery.price : 0;
          },
          getPaymentTitle(paymentOptionId) {
            const selectedPayment = this.paymentOptions.find(option => option.id === paymentOptionId);
            return selectedPayment ? selectedPayment.title : 'Не  выбрано';
          },
    },
    mounted() {
      this.fetchDeliveryOptions();
    },
    watch: {
      selectedDeliveryOption() {
        this.fetchPaymentOptions();
      },
    },
}
</script>