<template>
  <ul class="catalog__pagination pagination">
        <li class="pagination__item">
          <a href="#" class="pagination__link pagination__link--arrow" aria-label="Предыдущая страница"
          :class="{'pagination__link--disabled' : page == 1}" @click.prevent="prevPage()">

            <svg width="8" height="14" fill="currentColor">
              <use xlink:href="#icon-arrow-left"></use>
            </svg>
          </a>
        </li>
        <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
          <a href="#" class="pagination__link" :class="{'pagination__link--current': pageNumber === page}" @click.prevent="paginate(pageNumber)">
           {{ pageNumber}}
          </a>
        </li>

        <li class="pagination__item">
          <a class="pagination__link pagination__link--arrow" href="#" aria-label="Следующая страница"
          :class="{'pagination__link--disabled' : page == pages }" @click.prevent="nextPage(page)">
            <svg width="8" height="14" fill="currentColor">
              <use xlink:href="#icon-arrow-right"></use>
            </svg>
          </a>
        </li>
      </ul>
</template>

<script>
export default {
  props: ['modelValue', 'count', 'perPage'],
  computed: {
    page(){
      return this.modelValue;
    },
      pages(){
          return Math.ceil(this.count / this.perPage);
      }
  },
  methods: {
      paginate(page){
          this.$emit('update:modelValue', page);
      },
      nextPage(page) {
    if (page < this.pages) { this.$emit('update:modelValue', page + 1); }
  },
  prevPage() {
    if (this.page <= 1) {
      this.$emit('update:modelValue', this.page);
    } else {
      this.paginate(this.page - 1);
    }
  }
  }
}
</script>