<template>
    <h2 class="filter_title">Фильтры</h2>
        <form v-bind="$attrs" class="filter__form form" action="#" method="get" @submit.prevent="submit">
          <fieldset class="form__block">
            <legend class="form__legend">Цена</legend>
            <label class="form__label form__label--price">
              <input class="form__input" name="min-price" v-model.number="currentPriceFrom">
              <span class="form__value">От</span>
            </label>
            <label class="form__label form__label--price">
              <input class="form__input" name="max-price" v-model.number="currentPriceTo">
              <span class="form__value">До</span>
            </label>
          </fieldset>

          <fieldset class="form__block">
            <legend class="form__legend">Категория</legend>
            <label class="form__label form__label--select">
              <select class="form__select" name="category" v-model.number="currentCategoryId">
                <option value="0">Все категории</option>
                <option :value="category.id" v-for="category in categories" :key="category.id">{{ category.title }}</option>
              </select>
            </label>
          </fieldset>

          <fieldset class="form__block">
            <legend class="form__legend">Цвет</legend>
            <ul class="colors">
              <li class="colors__item" v-for="colorId in colors" :key="colorId.id">
                <label class="colors__label" >
                  <input class="colors__radio sr-only" type="radio" name="color" :value="colorId.id" v-model.number="currentColorId">
                  <span class="colors__value" :style="{backgroundColor: colorId.code}"></span>
                </label>
              </li>
            </ul>
          </fieldset>

          <fieldset class="form__block">
            <legend class="form__legend">Материал</legend>
            <ul class="check-list">          
              <li class="check-list__item" v-for="materialId in materials" :key="materialId.id">
                <label class="check-list__label">
                  <input class="check-list__check sr-only" type="checkbox" name="material" :value="materialId.id" v-model.number="currentMaterialId" >
                  <span class="check-list__desc">
                    {{materialId.title}} 
                    <span>({{materialId.productsCount}})</span>
                  </span>
                </label>
              </li>
            </ul>
          </fieldset>

          <fieldset class="form__block">
            <legend class="form__legend">Коллекция</legend>
            <ul class="check-list">
              <li class="check-list__item" v-for="sesonsId in seasons" :key="sesonsId.id">
                <label class="check-list__label">
                  <input class="check-list__check sr-only" type="checkbox" name="collection" :value="sesonsId.id" v-model.number="currentSeasonsId">
                  <span class="check-list__desc">
                    {{sesonsId.title}} 
                    <span>({{sesonsId.productsCount}})</span>
                  </span>
                </label>
              </li>
            </ul>
          </fieldset>

          <button class="filter__submit button button--primery" type="submit">
            Применить
          </button>
          <button class="filter__reset button button--second" type="button" @click.prevent="reset" v-if="resetSelected">
            Сбросить
          </button>
        </form>
</template>

<script>
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
    props:['priceFrom', 'priceTo', 'categoryId', 'colorId', 'materialsId', 'sesonsId'],
    data(){
        return {
            currentPriceFrom: 0,
            currentPriceTo: 0,
            currentCategoryId: 0,
            currentColorId: 0,
            currentMaterialId: [],
            currentSeasonsId: [],

            categoriesData: null,
            colorsData: null,
            materialsData: null,
            seasonsData: null
        }
    },
    computed: {       
        categories(){
            return this.categoriesData ? this.categoriesData.items : [];
        },
        colors(){
          return this.colorsData ? this.colorsData.items : [];
        },
        materials(){
          return this.materialsData ? this.materialsData.items : [];
        },
        seasons(){
          return this.seasonsData ? this.seasonsData.items : [];
        },
        resetSelected(){
          return (
          this.currentPriceFrom > 0 || 
          this.currentPriceTo > 0 || 
          this.currentCategoryId > 0 || 
          this.currentColorId > 0 ||
          this.currentMaterialId.length > 0 ||
          this.currentSeasonsId.length > 0
          );
        },
    },
    watch: {
        priceFrom(value){
            this.currentPriceFrom = value;
        },
        priceTo(value){
            this.currentPriceTo = value;
        },
        categoryId(value){
            this.currentCategoryId = value;
        },
        colorId(value){
          this.currentColorId = value;
        },
        materialsId(value){
          this.currentMaterialId = value;
        },
        sesonsId(value){
          this.currentSeasonsId = value;
        }
    },
    methods: {
        submit(){
            this.$emit('update:priceFrom', this.currentPriceFrom);
            this.$emit('update:priceTo', this.currentPriceTo);
            this.$emit('update:categoryId', this.currentCategoryId);
            this.$emit('update:colorId', this.currentColorId);
            this.$emit('update:materialsId', this.currentMaterialId);
            this.$emit('update:sesonsId', this.currentSeasonsId);
        },
        reset(){
            this.$emit('update:priceFrom', 0);
            this.$emit('update:priceTo', 0);
            this.$emit('update:categoryId', 0);
            this.$emit('update:colorId', 0);
            this.$emit('update:materialsId', []);
            this.$emit('update:sesonsId', []);
        },
        loadCategories(){
          axios.get(API_BASE_URL + '/api/productCategories')
              .then(response => this.categoriesData = response.data);
        },
        loadColors(){
          axios.get(API_BASE_URL + '/api/colors')
              .then(response => this.colorsData = response.data);
        },
        loadMaterials(){
          axios.get(API_BASE_URL + '/api/materials')
              .then(response => this.materialsData = response.data);
        },
        loadSeasons(){
          axios.get(API_BASE_URL + '/api/seasons')
              .then(response => this.seasonsData = response.data);
        }
    },
    created(){
      this.loadCategories();
      this.loadColors();
      this.loadMaterials();
      this.loadSeasons();
    }
}
</script>

<style>
.colors__item{display: contents;}
.colors__value{
  border: 1px solid #222;
}
</style>