<!-- eslint-disable vue/no-deprecated-router-link-tag-prop -->
<template>
<main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">
          Корзина
        </h1>
        <span class="content__info">
          {{$store.state.cartProducts.length}} товара
        </span>
      </div>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem v-for="item in products" :key="item.id" :item="item" :selectedColor="item.colorId"/>
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы посчитаем стоимость доставки на следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPricePretty }} ₽</span>
          </p>

          <router-link v-slot="{navigate}" v-if="$store.state.cartProducts.length > 0" :to="{name:'order'}" custom>
            <button class="cart__button button button--primery" type="button" @click="navigate">Оформить заказ</button>
            
          </router-link>
        </div>
      </form>
    </section>
  </main>

</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
import CartItem from '@/components/CartItem.vue';
import { defineComponent } from 'vue';

export default defineComponent({
    components: { CartItem },
    computed: {
      ...mapGetters({products:'cartDetailProducts', totalPrice: 'cartTotalPrice'}),
      totalPricePretty() {
        return numberFormat(this.totalPrice);
      },
    }
});
</script>