import numberFormat from '@/helpers/numberFormat';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import { computed, reactive, ref } from 'vue';


export default function(store) {
    const productData = ref(null);
    const currentSize = ref(null);
    const selectedColor = ref(null);
    const category = computed(() => {
    return productData.value.category;
    });

    const fetchStatus = reactive({
        isLoading: false,
        isFailed: false,
        isAddedProduct: false,
        isSendingProduct: false
    })
    const productImage = computed(() => {
        return image.value || 'img/no-image.jpg';
      });
    const size = computed(() => {
        return productData.value && productData.value.sizes ? productData.value.sizes : [];
      });
    
      const image = computed(() => {
        if (productData.value && selectedColor.value !== null) {
          const selectedColorObject = productData.value.colors.find(color => color.id === selectedColor.value);
    
          if (selectedColorObject) {
            const gallery = selectedColorObject.gallery;
    
            if (gallery && gallery.length > 0) {
              return gallery[0].file.url;
            }
          }
        }
    
        return 'img/no-image.jpg';
      });
    
      const productAmount = ref(1);
      const isShowAddedMessage = ref(false);
    
      const doAddToCart = () => {
        fetchStatus.isAddedProduct = false;
        fetchStatus.isSendingProduct = true;
        const selectedColorObject = productData.value.colors.find(color => color.id === selectedColor.value);
    
        store.dispatch('addProductToCart', {
          productId: productData.value.id,
          amount: productAmount.value,
          colorId: selectedColorObject ? selectedColorObject.color.id : null,
          sizeId: currentSize.value
        })
        .then(() => {
          isShowAddedMessage.value = true;
          fetchStatus.isAddedProduct = true;
          fetchStatus.isSendingProduct = false;
        });
      };

      const changeImage = (color) => {
        selectedColor.value = color.id;
      };
    
      const isCurrentColor = (color) => {
        return color.id === selectedColor.value;
      };
    
      const updateQuantity = (amount) => {
        productAmount.value = amount;
      };

    const productLoadTimer = ref();

    const fetchProduct = (productId) => {
            fetchStatus.isLoading = true;
            fetchStatus.isFailed = false;
            clearTimeout(productLoadTimer.value);
            productLoadTimer.value = setTimeout(() => {
            axios.get(API_BASE_URL + '/api/products/' + productId)
            .then(response => {
                const product = response.data;
                productData.value = Object.assign(product, {
                    pricePretty: numberFormat(product.price)
                });

                if (productData.value && productData.value.sizes && productData.value.sizes.length > 0) {
                currentSize.value = productData.value.sizes[0].id;
                }

                if (productData.value && productData.value.colors && productData.value.colors.length > 0) {
                selectedColor.value = productData.value.colors[0].id;
                }
            })
            .catch(() => fetchStatus.isFailed = true)
            .then(() => fetchStatus.isLoading = false);
            }, 70);
        };

        const generateSku = (productId, colorId, sizeId) => {
          return `0${productId}${colorId}${sizeId}`;
        };

    return { 
        product: productData,
        category,
        currentSize,
        selectedColor,
        size,
        image: productImage,
        isShowAddedMessage,
        changeImage,
        isCurrentColor,
        updateQuantity,
        status: fetchStatus,
        doAddToCart,
        fetchProduct,
        generateSku
    };
}