<template>
<router-link class="header__cart" aria-label="Корзина с товарами" :to="{name: 'cart'}">
        <svg width="19" height="24">
          <use xlink:href="#icon-cart"></use>
        </svg>
        <span v-if="loadCart" class="cart_preloader" aria-label="Количество товаров">
          <div >
              <div class="cart_spinner"></div>
          </div>
        </span>
        <span v-else-if="failedLoadCart">Произошла ошибка при загруске товаров.Попробовать еще раз</span>
        <span v-else class="header__count" aria-label="Количество товаров">{{$store.state.cartProducts.length}}</span>
</router-link> 
</template>

<script>
export default {
    computed: {
        loadCart() {
            return this.$store.state.cartLoad;
        },
        failedLoadCart() {
            return this.$store.state.cartLoadFailed;
        }
    }
}
</script>

<style>
.cart_preloader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 10%; 
}

.cart_spinner {
  border: 4px solid rgba(0, 0, 0, 0.1);
  border-left: 4px solid #d9276e;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
</style>