<template>
<div class="form__counter">
                <button type="button" aria-label="Убрать один товар" @click="decrement()">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <input type="text" v-model.number="productAmount">

                <button type="button" aria-label="Добавить один товар" @click="increment()">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
</div>
</template>

<script>
export default {
    props:{
        value:{
            default: 1
        }
    },
    data(){
        return{
            productAmount: this.value,
        }
    },
    methods: {
        decrement(){
            if(this.productAmount > 1){
                this.productAmount -= 1;
                this.$emit('productAmount-changed', this.productAmount);
            }
        },
        increment(){
            this.productAmount += 1;
            this.$emit('productAmount-changed', this.productAmount);
        }
    }
}

</script>