<template>
<li class="cart__item product">
              <div class="product__pic" v-if="imageUrl">
                <img :src="imageUrl " width="120" height="120" :alt="item.product.title">
              </div>
              <h3 class="product__title">
                {{item.product.title}}
              </h3>
              
              <p class="product__info product__info--color">
                
                Цвет:
                <span class="colors__item" v-if="color">
                  <i class="colors__value" :style="{ background: color.color.code }"></i>
                  {{ color.color.title }}
              </span>
                Размер:
                <span v-if="size" class="size__item"> {{size.title  }}</span>
              </p>
              <span class="product__code">
                SKU: {{ sku }}
              </span>
              <div class="product__counter form__counter">
                <ProoductQnty :value="amount" @productAmount-changed="amount = $event"/>
              </div>

              <b class="product__price">
                {{ totalPricePretty }} ₽
              </b>

              <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="removeProduct(item.basketItemId)">
                <svg width="20" height="20" fill="currentColor">
                  <use xlink:href="#icon-close"></use>
                </svg>
              </button>
            </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import ProoductQnty from './ProoductQnty.vue';
import useProduct from '@/hooks/useProduct';

export default {
    props: ['item'],
    setup(props){
      const { generateSku } = useProduct();
      const sku = generateSku(props.item.product.id, props.item.colorId, props.item.sizeId);
      return {
        sku
      };
    },
    computed: {
      totalPricePretty() {
        return numberFormat(this.item.amount * this.item.product.price);
      },
        amount: {
            get() {
                return this.item.amount;
            },
            set(value) {
                this.$store.dispatch('updateCartProductAmount', { basketItemId: this.item.basketItemId, amount: value });
            }
        },
        imageUrl(){          
          return this.color.gallery && this.color.gallery.length > 0 ? this.color.gallery[0].file.url : '../img/no-image.jpg';
        },
        color(){
          return this.item.product.colors.find(c => c.color.id === this.item.colorId);
        },
        size() {
          return this.$store.state.cartProductsData.find(s => s.size.id === this.item.sizeId).size;
        },
        
    },
    methods: {
        removeProduct(basketItemId) {
            this.$store.dispatch('deleteCart', basketItemId);
        },
    },
    components: { ProoductQnty }
}
</script>

<style>
.colors__item{display: contents;}
.colors__value{border: 1px solid #222;}
.size__item{padding-left: 0px!important; font-weight: bold;}
</style>